.dk_container {
  width:100%;
  float: left;
}
.dk_container a {
  display: block;
  height:50px;
  background-color: #6885a2;
  color:#fff;
  line-height: 50px;
  outline: 0;
  cursor: pointer;
  padding:0px 10px;
  overflow: hidden;
}
.dk_container.dk_theme_default.dk_open{
  color:#fff;
}
.dk_toggle {
  display: -moz-inline-stack;
  display: inline-block;
  *display: inline;
  position: relative;
  zoom: 1; }

.dk_open {
  position: relative; }
  .dk_open .dk_options {
    display: block; }
  .dk_open .dk_label {
    color: inherit; }

.dk_options {
  display: none;
  margin-top: -1px;
  position: absolute;
  right: 0;
  width: 100%; }
  .dk_options a, .dk_options a:link, .dk_options a:visited {
    display: block; }

.dk_options_inner {
  overflow: auto;
  position: relative;
  -webkit-overflow-scrolling: touch;
  list-style-type: none;
  margin:0px;
  padding:0px;
}
.dk_options_inner li a{
  background-color:#fff;
  color:#6885a2;
  height:25px;
  line-height: 25px;
  white-space: nowrap;
  overflow:hidden;
}
.dk_options_inner li a:hover{
  background-color:#6885a2;
  color:#fff;
}

.dk_container select {
  position: absolute;
  top: -99999em;
  visibility: hidden; }

.dk_mobile {
  position: relative; }
  .dk_mobile select {
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    visibility: visible;
    opacity: 0;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none; }




body.page-contains-wizard .slide .questions{
  
  .dk_container.dk_theme_default.dk_open{
    color:#556270;
  }
  .dk_container {
    background-color: #f5f5f5;
    font-size: 14px;
    font-weight: normal;
    margin-bottom: 18px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    width: inherit;
  }
  .dk_container:focus {
    outline: 0; 
  }
  .dk_container a {
    cursor: pointer;
    font-family: 'latoregular', sans-serif;
    font-weight: normal;
    text-decoration: none; 
    background-color: #F5F5F5;
    line-height: 30px;
    height: auto;
  }
  .dk_toggle {
    border: 1px solid #ccc;
    color: #333;
    padding: 7px 45px 7px 10px;
    text-shadow: #fff 1px 1px 0;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    -webkit-transition: border-color .5s;
    -moz-transition: border-color .5s;
    -o-transition: border-color .5s;
    transition: border-color .5s;
    position: relative;
  }
  .dk_toggle:hover, .dk_toggle:focus {
    border-color: #8c8c8c; 
  }
  .dk_toggle:after {
    position: absolute;
    top: 45%;
    right: 10px;
    content: '';
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid #4B4B4B; 
  }

  .dk_focus .dk_toggle {
    border-color: #40b5e2;
    box-shadow: 0 0 5px #40b5e2;
    -moz-box-shadow: 0 0 5px #40b5e2;
    -webkit-box-shadow: 0 0 5px #40b5e2; 
  }

  .dk_open {
    box-shadow: 0 0 5px #40b5e2;
    -moz-box-shadow: 0 0 5px #40b5e2;
    -webkit-box-shadow: 0 0 5px #40b5e2;
    z-index: 10; 
  }
  
  .dk_open .dk_toggle {
    background-color: #ececec;
    border-color: #8c8c8c;
    color: #ccc;
    box-shadow: inset 0 -2px 5px #ccc;
    border-radius: 5px 5px 0 0;
    -moz-border-radius: 5px 5px 0 0;
    -webkit-border-radius: 5px 5px 0 0; 
  }

  .dk_open_top .dk_toggle {
    border-radius: 0 0 5px 5px;
    -moz-border-radius: 0 0 5px 5px;
    -webkit-border-radius: 0 0 5px 5px; 
  }

  .dk_open_top .dk_options, .dk_open_top .dk_options_inner {
    border-radius: 5px 5px 0 0;
    -moz-border-radius: 5px 5px 0 0;
    -webkit-border-radius: 5px 5px 0 0; 
  }

  .dk_options {
    background: #fefefe;
    box-shadow: rgba(0, 0, 0, 0.2) 0 2px 8px;
    -moz-box-shadow: rgba(0, 0, 0, 0.2) 0 2px 8px;
    -webkit-box-shadow: rgba(0, 0, 0, 0.2) 0 2px 8px;
    border-radius: 0 0 5px 5px;
    -moz-border-radius: 0 0 5px 5px;
    -webkit-border-radius: 0 0 5px 5px; 
  }
  
  .dk_options a {
    background-color: #fff;      
    border-bottom: 1px solid #999;
    font-weight: normal;
    padding: 8px 10px; 
  }
  
  .dk_options a:hover, .dk_options:focus,
  .dk_options .dk_option_current a {
    background-color: #0084c7;
    border-bottom-color: #004c72;
    color: #fff;
    text-decoration: none;
    text-shadow: rgba(0, 0, 0, 0.5) 0 1px 0; 
  }
  
  .dk_options li:last-child a {
    border-bottom: none; 
  }
  
  .dk_options .disabled {
    cursor: default; 
  }
  
  .dk_options .disabled a {
    color: #aaa; 
  }
  
  .dk_options .disabled:hover, .dk_options .disabled:hover a
  .dk_options .disabled:focus, .dk_options .disabled:focus a {
    cursor: default;
    background-color: #fff;
    border-bottom-color: #999;
    text-shadow: none; 
  }

  .dk_options_inner {
    max-height: 250px;
    border: 1px solid #8c8c8e;
    border-bottom-width: 2px;
    border-bottom-color: #999;
    color: #333;
    text-shadow: #fff 0 1px 0;
    border-radius: 0 0 5px 5px;
    -moz-border-radius: 0 0 5px 5px;
    -webkit-border-radius: 0 0 5px 5px; 
  }

